.matches-grid {
    list-style: none;
    padding: 10px;
    margin: 0;
    box-sizing: border-box;
    
    display: grid;
    grid-template-columns: repeat(3, 1fr); 
    gap: 15px; 
}

.match-tile {
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    background-color: #ffffff; 
    
    padding: 0 0 8px 0; 
    border-radius: 18px; 
    overflow: hidden; 

    transition: 
        transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
        box-shadow 0.3s ease;
}

.match-image-container {
    width: 100%;
    position: relative;
    overflow: hidden;
    padding-top: 100%; 
}

.match-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
    
    border: 3px solid white; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.match-name-small {
    margin-top: 8px;
    font-size: 0.9rem;
    font-weight: 700;
    color: #333;
    text-align: center;
    z-index: 5;
}

.match-tile:hover {
    transform: scale(1.08); 
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2); 
    z-index: 10;
}

.match-tile:active {
    transform: scale(1.03); 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); 
}

.empty-state {
    grid-column: 1 / -1;
    text-align: center;
    color: #999;
    padding: 20px;
    font-style: italic;
}