@keyframes slideInFade {
    0% {
        opacity: 0;
        transform: translateY(-50px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.login-view {
    min-height: 100vh;
    width: 100%;
    background: url('../../resources/images/people-background.jpeg') center / cover no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-overlay {
    width: 90%;
    max-width: 1200px;
    height: 600px;
    background: rgba(1, 0, 31, 0.68);
    border-radius: 22px;
    display: flex;
    overflow: hidden;
    box-shadow: none;
    animation: slideInFade 0.8s ease-out forwards;
}

.login-left {
    flex: 1.3;
    color: white;
    padding: 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    animation: fadeIn 1.2s ease-out 0.2s forwards;
    opacity: 0; 
}

.login-icon {
    width: 100%;
    height: auto;
    margin: auto;
    margin-bottom: 20px;
    filter: none; 
    border: none;
}

.login-features {
    list-style: none;
    padding: 0;
    margin: 0;
}

.login-features li {
    font-size: 18px;
    margin-bottom: 18px;
    opacity: 0.95;
    display: flex;
    align-items: center;
    gap: 12px;
    line-height: 1.4;
}

.login-features li::before {
    content: "";
    color: #ff6b35;
    font-weight: bold;
    font-size: 18px;
}

.login-right {
    flex: 0.7;
    background: white;
    padding: 60px 45px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.login-right h2, 
.login-right p,
.login-right input,
.login-right button,
.login-register {
    animation: fadeIn 1.2s ease-out forwards;
    opacity: 0;
}

.login-right h2 { animation-delay: 0.4s; }
.login-right p { animation-delay: 0.5s; }
.login-right input:nth-of-type(1) { animation-delay: 0.6s; } 
.login-right input:nth-of-type(2) { animation-delay: 0.7s; } 
.login-right button { animation-delay: 0.8s; }
.login-register { animation-delay: 0.9s; }


.login-right a {
    color: #ff6b35;
    text-decoration: none;
}

.login-right a:hover {
    text-decoration: underline;
}

.login-right h2 {
    font-size: 34px;
    margin-bottom: 10px;
}

.login-right p {
    margin-bottom: 32px;
    color: #666;
    font-size: 15px;
}

.login-right input {
    height: 50px;
    margin-bottom: 18px;
    border-radius: 14px;
    border: none;
    background: #f2f2f2;
    padding: 0 18px;
    font-size: 15px;
}

.login-right button {
    height: 52px;
    border: none;
    border-radius: 16px;
    background: linear-gradient(135deg, #0A4DAD, #083a86);
    color: white;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    margin-top: 12px;
    transition: 0.25s ease;
}

.login-right button:hover {
    transform: scale(1.04);
    filter: brightness(1.1);
}

.login-register {
    margin-top: 22px;
    font-size: 14px;
    text-align: center;
    color: #777;
}

.login-register b {
    color: #ff6b35;
    cursor: pointer;
    transition: 0.2s;
}

.login-register b:hover {
    text-decoration: underline;
}

@media (max-width: 900px) {
    .login-overlay {
        flex-direction: column;
        height: auto;
        max-width: 500px;
        border-radius: 16px;
    }

    .login-left {
        padding: 40px;
        align-items: center;
        text-align: center;
        flex: auto;
        opacity: 1; 
        animation: none;
    }

    .login-icon {
        margin-bottom: 30px;
    }

    .login-features li {
        justify-content: center;
        font-size: 16px;
    }

    .login-right {
        padding: 40px 30px;
        flex: auto;
    }

    .login-right h2 {
        font-size: 28px;
    }
    
    .login-right h2, 
    .login-right p,
    .login-right input,
    .login-right button,
    .login-register {
        animation: none;
        opacity: 1;
    }
}

@media (max-width: 550px) {
    .login-overlay {
        width: 95%;
        border-radius: 12px;
        margin: 20px 10px;
    }

    .login-left, .login-right {
        padding: 20px 20px;
    }

    .login-right h2 {
        font-size: 24px;
    }

    .login-features li {
        font-size: 15px;
    }
}