.about-view {
    min-height: 100vh;
    background: #ffffff;
    display: flex;
    justify-content: center;
    padding: 120px 20px;
    color: #1f1f1f;
}

.about-wrapper {
    max-width: 900px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 120px;
}

.about-intro h1 {
    font-size: 64px;
    line-height: 1.1;
    font-weight: 800;
    letter-spacing: 2px;
    margin-bottom: 40px;
    color: var(--secondary-color);
}

.about-intro p {
    font-size: 26px;
    line-height: 1.6;
    color: var(--primary-color);
}

.about-block h2 {
    font-size: 42px;
    font-weight: 700;
    letter-spacing: 2px;
    margin-bottom: 24px;
    color: var(--secondary-color);
}

.about-block p {
    font-size: 24px;
    line-height: 1.7;
    color: var(--primary-color);
}

.about-quote {
    font-size: 32px;
    font-style: italic;
    line-height: 1.5;
    border-left: 6px solid var(--secondary-color);
    padding-left: 40px;
    color: var(--primary-color);
}

.about-ending h2 {
    font-size: 54px;
    font-weight: 800;
    margin-bottom: 30px;
    color: var(--secondary-color);
}

.about-ending p {
    font-size: 26px;
    line-height: 1.6;
    color: var(--primary-color);
}

.about-highlight.tech-highlight {
    position: relative;
    padding: 100px 60px;
    border-radius: 40px;
    background: rgba(0,0,0,0.03);
    font-size: 36px;
    font-weight: 900;
    text-align: center;
    color: var(--secondary-color);
    perspective: 1200px;
    overflow: hidden;

    transform-style: preserve-3d;
}

.about-highlight.tech-highlight p {
    position: relative;
    z-index: 2;
}

.about-highlight.tech-highlight::after {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 40px;
    background: radial-gradient(circle at 50% 50%, rgba(255,255,255,0.08), transparent 70%);
    pointer-events: none;
    z-index: 1;
    animation: neonWave 4s linear infinite;
}

@keyframes neonWave {
    0% { background-position: -150% 0; }
    50% { background-position: 150% 0; }
    100% { background-position: -150% 0; }
}

@media (max-width: 900px) {
    .about-wrapper { gap: 80px; }

    .about-intro h1 { font-size: 42px; }

    .about-intro p,
    .about-block p,
    .about-ending p { font-size: 20px; }

    .about-block h2 { font-size: 32px; }

    .about-highlight.tech-highlight {
        font-size: 22px;
        padding: 50px 30px;
    }

    .about-quote { font-size: 22px; }

    .about-ending h2 { font-size: 38px; }
}
