.window{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;
    width: 100%;
    flex-direction: column;
}

.window .loader {
    border: 8px solid #f3f3f3; 
    border-top: 8px solid var(--primary-color); 
    border-radius: 50%; 
    width: 50px;
    height: 50px;
    animation: spin 1.5s linear infinite; 
}