.textbox-wrapper {
    width: 100%;
    display: flex;
}

.textbox-input {
    width: 100%;
    padding: 14px 16px;
    border-radius: 12px;
    border: none;
    background: #f2f2f2;
    font-size: 15px;
    outline: none;
    transition: 0.2s ease;
}

.textbox-input::placeholder {
    color: #9a9a9a;
}

.textbox-input:focus {
    background: #eaeaea;
    box-shadow: 0 0 0 2px var(--secondary-color);
}