@keyframes slideInFade {
    0% {
        opacity: 0;
        transform: translateY(-50px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.register-view {
    min-height: 100vh;
    width: 100%;
    background: url('../../resources/images/people-background.jpeg') center / cover no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}

.register-overlay {
    width: 90%;
    max-width: 1200px;
    height: 700px; 
    background: rgba(1, 0, 31, 0.68);
    border-radius: 22px;
    display: flex;
    overflow: hidden;
    box-shadow: none;

    animation: slideInFade 0.8s ease-out forwards;
}

.register-left {
    flex: 1.3;
    color: white;
    padding: 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    animation: fadeIn 1.2s ease-out 0.2s forwards;
    opacity: 0; 
}

.register-left h3 {
    font-size: 26px;
    margin-bottom: 30px;
}

.register-icon {
    width: 100%;
    height: auto;
    margin: auto;
    margin-bottom: 20px;
    filter: none; 
    border: none;
}

.register-features {
    list-style: none;
    padding: 0;
    margin: 0;
}

.register-features li {
    font-size: 18px;
    margin-bottom: 18px;
    opacity: 0.95;
    display: flex;
    align-items: center;
    gap: 12px;
    line-height: 1.4;
}

.register-features li::before {
    content: ""; 
    color: #ff6b35;
    font-weight: bold;
    font-size: 18px;
}

.register-right {
    flex: 0.7;
    background: white;
    padding: 60px 45px;
    display: flex;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
}

.register-right h2, 
.register-right p,
.register-right input,
.register-right .register-textarea,
.register-right button,
.register-right .register-actions,
.register-right .register-back {
    animation: fadeIn 0.6s ease-out forwards;
    opacity: 0;
}

.register-right h2 { animation-delay: 0.2s; }
.register-right p { animation-delay: 0.3s; }

.register-right h2 {
    font-size: 34px;
    color: #ff6b35;
    margin-bottom: 10px;
}

.register-right p {
    margin-bottom: 32px;
    color: #666;
    font-size: 15px;
}

.register-right input {
    animation-delay: 0.4s;
}

.register-textarea {
    width: 100%;
    min-height: 120px;
    margin-bottom: 18px;
    border-radius: 14px;
    border: none;
    background: #f2f2f2;
    padding: 18px;
    font-size: 15px;
    resize: vertical;
    box-sizing: border-box;
    animation-delay: 0.4s;
}

.register-actions {
    display: flex;
    gap: 15px;
    margin-top: 15px;
    animation-delay: 0.5s;
}

.register-actions button {
    flex: 1; 
}

.register-back {
    margin-top: 22px;
    font-size: 14px;
    text-align: center;
    color: #777;
    cursor: pointer;
    display: block;
    animation-delay: 0.6s;
}

.register-back:hover {
    color: #ff6b35;
}

@media (max-width: 900px) {
    .register-overlay {
        flex-direction: column;
        height: auto;
        max-width: 500px;
        border-radius: 16px;
    }

    .register-left {
        padding: 40px;
        align-items: center;
        text-align: center;
        flex: auto;
        opacity: 1; 
        animation: none;
    }

    .register-left h3 {
        font-size: 24px;
    }

    .register-icon {
        margin-bottom: 30px;
    }

    .register-features li {
        justify-content: center;
        font-size: 16px;
    }

    .register-right {
        padding: 40px 30px;
        flex: auto;
    }

    .register-right h2 {
        font-size: 28px;
    }
    
    .register-right h2, 
    .register-right p,
    .register-right input,
    .register-right .register-textarea,
    .register-right button,
    .register-right .register-actions,
    .register-right .register-back {
        animation: none;
        opacity: 1;
    }
}

@media (max-width: 550px) {
    .register-overlay {
        width: 95%;
        border-radius: 12px;
        margin: 20px 10px;
    }

    .register-left, .register-right {
        padding: 20px 20px;
    }

    .register-right h2 {
        font-size: 24px;
    }

    .register-features li {
        font-size: 15px;
    }
}