.not-found-view {
    background: var(--third-color);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 120px 0;
    color: var(--dark-gray);
    overflow: hidden;
}

.not-found-wrapper {
    max-width: 1300px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 80px;
}

.not-found-content {
    flex: 1;
}

.error-code {
    font-size: 120px;
    font-weight: 900;
    line-height: 1;
    color: var(--light-gray);
    margin-bottom: 20px;
    letter-spacing: -5px;
}

.not-found-content h1 {
    font-size: 64px;
    line-height: 1.1;
    font-weight: 700;
    letter-spacing: 2px;
    margin-bottom: 30px;
    color: var(--secondary-color);
}

.not-found-content p {
    font-size: 24px;
    line-height: 1.6;
    color: var(--primary-color);
    margin-bottom: 50px;
}

.back-home-btn {
    padding: 15px 20px;
    font-size: 22px;
    font-weight: 700;
    letter-spacing: 1px;
    color: var(--third-color);
    background-color: var(--secondary-color);
    border: none;
    border-radius: 15px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 10px 20px rgba(249, 115, 22, 0.2);
}

.back-home-btn:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 30px rgba(249, 115, 22, 0.4);
    filter: brightness(1.1);
}

.not-found-image-container {
    flex: 1;
    display: flex;
    justify-content: center;
}

.image-frame {
    position: relative;
    width: 100%;
    max-width: 500px;
    border-radius: 40px;
    overflow: hidden;
    box-shadow: 25px 25px 0px var(--light-gray); 
}

.image-frame img {
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
}

@media (max-width: 1000px) {
    .not-found-wrapper {
        flex-direction: column-reverse;
        text-align: center;
        gap: 60px;
    }

    .not-found-content h1 {
        font-size: 48px;
    }

    .not-found-content p {
        font-size: 20px;
    }

    .error-code {
        font-size: 80px;
    }

    .image-frame {
        max-width: 300px;
        box-shadow: 15px 15px 0px var(--light-gray);
    }

    .back-home-btn {
        padding: 15px 0;
        width: 80%;
        font-size: 19px;
    }
}