.home-view {
    display: flex;
    flex-direction: column;
    row-gap: 120px;
    height: 100%;
    background-color: #ffffff;
    color: #1f1f1f;
    padding: 70px 40px;
    box-sizing: border-box;
}

/* sections */
.home-view .section {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: fit-content;
    width: 100%;
    gap: 20px;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.8s ease, transform 0.8s ease;
}

.home-view .section.visible {
    opacity: 1;
    transform: translateY(0);
}

.home-view .section.reverse {
    flex-direction: row-reverse;
}

.home-view .section .img-container {
    height: 450px;
    width: 55%;
    min-width: 50%;
    border-radius: 20px;
    overflow: hidden;
    transition: transform 0.5s ease, box-shadow 0.5s ease;
}

.home-view .section .img-container.hover-card:hover {
    transform: translateY(-10px) scale(1.02) rotate(-1deg);
    box-shadow: 0 20px 40px rgba(0,0,0,0.25);
}

.home-view .section .img-container img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.home-view .section .center {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 120px;
    text-shadow: 2px 2px 10px rgba(43, 43, 43, 0.3);
    text-align: center;
    width: 40%; 
}

.looping-title {
    position: relative;
    height: 160px; 
    width: 100%;
    overflow: hidden;
    display: block;
}

.animated-line {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, 100%); 
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 65px;
    line-height: 1;
    color: var(--secondary-color); 
    opacity: 0; 
    
    animation: loopShow 9s linear infinite;
}

.animated-line:nth-child(1) { animation-delay: 0s; }
.animated-line:nth-child(2) { animation-delay: 3s; }
.animated-line:nth-child(3) { animation-delay: 6s; }

@keyframes loopShow {
    0%   { opacity: 0; transform: translate(0, 100%); } 

    5.5% { opacity: 1; transform: translate(0, 0); } 

    33.3% { opacity: 1; transform: translate(0, 0); } 

    38.8% { opacity: 0; transform: translate(0, -100%); } 

    100% { opacity: 0; transform: translate(0, -100%); }
}

.home-view .section .center .title-65 {
    letter-spacing: 4px;
}

.home-view .section .left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    align-self: flex-start;
    height: 100%;
    width: fit-content;
    text-align: left;
    text-shadow: 2px 2px 10px rgba(43, 43, 43, 0.3);
}

.home-view .section .left .title-50 {
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 4px;
    color: var(--secondary-color); 
    margin-bottom: 20px;
}

.home-view .section .left .description {
    font-size: 30px;
    font-weight: 400;
    letter-spacing: 2px;
    color: var(--primary-color); 
}

@media (max-width: 1000px) {
    .home-view .section {
        flex-direction: column;
        gap: 30px;
    }
    .home-view .section.reverse {
        flex-direction: column;
    }
    .home-view .section .img-container {
        width: 100%;
        height: 350px;
    }

    .home-view .section .center {
        width: 100%; 
    }

    .looping-title {
        height: 120px; 
    }

    .home-view .section .left .title-50 {
        font-size: 40px;
    }
    .home-view .section .left .description {
        font-size: 22px;
    }
    .home-view .section .center .title-65 {
        font-size: 50px;
    }
    .animated-line {
        font-size: 50px;
    }
}