.swipe-view-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%; 
    padding: var(--card-padding);
    background-color: #fafafa;
    box-sizing: border-box;
}

.swipe-view-stack {
    position: relative;
    width: 100%;
    max-width: 400px; 
    height: 600px; 
    margin-bottom: 40px; 
}

.swipe-card {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background-color: var(--third-color); 
    opacity: 1; 
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    overflow: hidden; 
    
    transition: transform 0.3s ease-out, opacity 0.3s ease-out, box-shadow 0.3s ease;
    
    will-change: transform;
    user-select: none;
    touch-action: pan-x; 
}

.swipe-card.is-scrolling {
    touch-action: pan-y;
}

.swipe-card.is-top {
    cursor: grab;
    pointer-events: auto;
}

.swipe-card.is-top:active {
    cursor: grabbing;
}

.swipe-view-stack .swipe-card:nth-last-child(2) {
    transform: scale(0.95) translateY(10px);
    opacity: 1; 
    z-index: 1; 
}

.swipe-card.is-under {
    pointer-events: none;
}

.card-content-wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: white; 
}

.card-photo-container {
    width: 100%;
    padding: var(--card-padding);
    padding-bottom: 0; 
    box-sizing: border-box;
    flex-shrink: 0; 
    background-color: white;
}

.card-photo {
    width: 100%;
    aspect-ratio: 1 / 1; 
    object-fit: cover;
    border-radius: 15px; 
    display: block;
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
    pointer-events: none; 
    user-select: none;
}

.card-scroll-content {
    flex-grow: 1;
    padding: var(--card-padding);
    padding-bottom: 50px; 
    padding-top: 15px; 
    background-color: var(--third-color); 
    
    overflow-y: auto;
    overflow-x: hidden;
    touch-action: pan-y; 
    box-sizing: border-box;
}

.card-scroll-content::-webkit-scrollbar {
    width: 6px;
}
.card-scroll-content::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 10px;
}

.info-header h2 {
    margin: 0 0 10px 0;
    font-size: 2.2rem;
    line-height: 1.2;
    color: var(--dark-gray);
}
.info-header .user-age {
    font-weight: 300;
    font-size: 1.8rem;
    color: var(--dim-gray);
    margin-left: 5px;
}
.info-section {
    margin-bottom: 25px;
}
.section-title {
    font-size: 1.1rem;
    margin: 0 0 10px 0;
    font-weight: 600;
    color: var(--dark-gray); 
    border-left: 4px solid var(--secondary-color); 
    padding-left: 10px;
}
.bio-text {
    line-height: 1.6;
    color: #444;
}
.preferences-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 7px;
}
.preference-tag-mini {
    display: inline-block;
    background-color: #f0f4f8; 
    color: var(--dark-gray);
    padding: 5px 10px;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 600;
    border: 1px solid #eef;
}
.style-details {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
}
.style-detail-item {
    font-size: 0.9rem;
    font-weight: 400;
    padding: 5px 10px;
    background-color: #f0f4f8; 
    border-radius: 5px;
    color: var(--dark-gray);
}
.style-value {
    font-weight: 700;
    color: var(--primary-color); 
}

.swipe-label {
    position: absolute;
    top: 50px;
    padding: 10px 15px;
    border-radius: 10px;
    font-size: 2rem;
    font-weight: bold;
    opacity: 0;
    z-index: 15;
    background: rgba(255,255,255,0.8); 
    pointer-events: none;
}
.swipe-label-dislike {
    left: 20px;
    border: 4px solid red;
    color: red;
    transform: rotate(-10deg);
}
.swipe-label-like {
    right: 20px;
    border: 4px solid var(--primary-color);
    color: var(--primary-color);
    transform: rotate(10deg);
}

.swipe-controls-overlay {
    position: absolute;
    bottom: -32px; 
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center; 
    gap: 40px; 
    z-index: 20; 
    pointer-events: none; 
}

.swipe-button {
    border: none;
    aspect-ratio: 1 / 1;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); 
    transition: transform 0.15s ease-out, box-shadow 0.15s ease-out;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    
    width: 64px; 
    height: 64px;
    
    pointer-events: auto; 
}

.swipe-button span { 
    font-size: 1.8rem;
}

.swipe-button:hover:not(:disabled) {
    transform: scale(1.1);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
}

.dislike {
    background-color: var(--third-color);
    border: 2px solid #ff4d4d;
    color: #ff4d4d;
}

.like {
    background-color: var(--primary-color);
    border: 2px solid var(--primary-color);
    color: var(--third-color);
}

.empty-swipe-card {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background-color: var(--third-color);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    z-index: 0;
}

.empty-state-content h2 {
    color: var(--dark-gray);
    margin-bottom: 10px;
}

.empty-hint {
    color: var(--dim-gray);
    font-size: 0.9rem;
    margin-top: 20px;
}

.match-overlay-container {
    position: fixed; 
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100; 
    pointer-events: auto;
    animation: fadeIn 0.3s ease-out;
}

.match-overlay-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); 
    backdrop-filter: blur(5px);
}

.match-overlay-content {
    position: relative;
    background-color: var(--third-color);
    border-radius: 25px;
    padding: 30px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
    text-align: center;
    max-width: 85%;
    transform: scale(0.8);
    animation: popIn 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55) forwards;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes popIn {
    0% { transform: scale(0.8); opacity: 0; }
    100% { transform: scale(1); opacity: 1; }
}

.match-celebration {
    margin-bottom: 20px;
}

.match-icon {
    font-size: 5rem;
    display: block;
    animation: pulse 1s infinite alternate;
}

@keyframes pulse {
    from { transform: scale(1); }
    to { transform: scale(1.1); }
}

.match-info {
    margin-bottom: 40px;
}

.match-info h1 {
    color: var(--primary-color);
    font-size: 2.5rem;
    margin: 10px 0;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.matched-user-name {
    font-weight: bold;
    color: var(--primary-color);
}

.match-photos {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin: 20px 0;
}

.profile-photo, .matched-photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid var(--primary-color);
    box-shadow: 0 0 0 5px white, 0 5px 15px rgba(0, 0, 0, 0.2);
}

.profile-photo {
    border-color: var(--secondary-color); 
}